#loading-animation{
   
    margin: 0px auto;
}

.rainbow-loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10vh; /* Set the height to occupy the entire viewport height */
    
  }
  
  /* Hide the loader initially */
  .hidden {
    display: none;
  }
  
  /* Show the loader when isLoading is true */
  .visible {
    display: flex; /* Use flex to show the loader */
  }